
import { defineComponent, computed, ref, PropType, onMounted } from 'vue'
// MessageVM is an alias of vue.VNode
const TypeMap = {
  success: 'success',
  info: 'info',
  warning: 'warning',
  error: 'error'
}
export default defineComponent({
  name: 'ElMessage',
  props: {
    customClass: { type: String, default: '' },
    center: { type: Boolean, default: false },
    dangerouslyUseHTMLString: { type: Boolean, default: false },
    duration: { type: Number, default: 3000 },
    iconClass: { type: String, default: '' },
    id: { type: String, default: '' },
    message: {
      type: [String, Object] as PropType<string | any>,
      default: ''
    },
    onClose: {
      type: Function as PropType<() => void>,
      required: true
    },
    showClose: { type: Boolean, default: false },
    type: { type: String, default: 'info' },
    offset: { type: Number, default: 20 },
    zIndex: { type: Number, default: 0 }
  },
  emits: ['destroy'],
  setup(props) {
    const typeClass = computed(() => {
      const type = props.type as string
      return type && TypeMap[type] ? `el-message__icon el-icon-${TypeMap[type]}` : ''
    })
    const customStyle = computed(() => {
      return {
        top: `${props.offset}px`,
        zIndex: props.zIndex
      }
    })

    const visible = ref(false)
    let timer = null

    function startTimer() {
      if (props.duration > 0) {
        timer = setTimeout(() => {
          if (visible.value) {
            close()
          }
        }, props.duration as number)
      }
    }

    function clearTimer() {
      clearTimeout(timer)
      timer = null
    }

    function close() {
      visible.value = false
    }

    onMounted(() => {
      startTimer()
      visible.value = true
    })
    return {
      typeClass,
      customStyle,
      visible,

      close,
      clearTimer,
      startTimer
    }
  }
})
